import React, { createElement, Fragment, useEffect, useState } from 'react'
import { Link, Element } from 'react-scroll'
import { useMeasure, useWindowSize } from 'react-use'
import styled from '@emotion/styled'
import Headroom from 'react-headroom'
import loadable from '@loadable/component'

import Container from '../../components/Container'
import Text from '../../components/Text'
import Box from '../../components/Box'
import theme, { Media, responsive } from '../../components/ThemeProvider/theme';

import Layout from '../Layout';

// import FocusOne from './FocusOne';
// import FocusTwo from './FoucusTwo'
// import NewMarket from './NewMarket'
// import Esg from './Esg'
// import KeyPoint from './KeyPoint'

import Banner from './Banner'

const FocusOne = loadable(() => import('./FocusOne'))
const FocusTwo = loadable(() => import('./FoucusTwo'))
const NewMarket = loadable(() => import('./NewMarket'))
const Esg = loadable(() => import('./Esg'))
const KeyPoint = loadable(() => import('./KeyPoint'))
const Fund = loadable(() => import('./Fund'))
const Buy = loadable(() => import('./Buy'))
const FloatButton = loadable(() => import('../FloatButton'))

// const duration = 0.3
// const disableScroll = () => {
//   window.document.body.style.overflow = 'hidden'
//   window.document.body.style.height = '100%'
// }

// const enableScroll = () => {
//   window.document.body.style.overflow = 'auto'
//   window.document.body.style.height = 'auto'
// }

const StyledHeadroom = styled(Headroom)`
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-Index: 1;
  .headroom--scrolled {
    transition: transform 200ms ease-in-out;
  }
  .headroom--unpinned {
    transform: translateY(100%);
  }
  .headroom--pinned {
    transform: translateY(0%);
  }
`

const pages = [
  'section-focus',
  'ESG-green',
  'new-market',
  'target',
  'section-subject',
  'section-fund',
  'section-buy'
]

const comps = [
  KeyPoint,
  FocusOne,
  FocusTwo,
  NewMarket,
  Esg,
  Fund,
  Buy,
]

const IndexPage = () => {
  const [ref, { height }] = useMeasure();
  const [page, setPage] = useState()
  const windowSize = useWindowSize()
  const [windowHeight, setWindowheight] = useState(windowSize.height)
  useEffect(() => {
    // android
    if (Math.abs(windowSize.height - windowHeight) > 80) {
      setWindowheight(windowSize.height)
    }
  }, [windowSize.height, windowHeight])
  return (
    <Layout>
      <Box.Absolute top="0" left="0" transform="translateX(-100%)">
        {pages.map((name) => (
          <Link
            spy
            offset={-360}
            to={name}
            onSetActive={(to) => setPage(to)}
            key={name}
            hashSpy
          />
        ))}
      </Box.Absolute>
      <Box height={responsive(`calc(${windowHeight - height}px - ${theme.headerHeight})`, `calc(${windowHeight}px - ${theme.headerHeight})`)}>
        <Banner />
      </Box>
      {comps.reduce((c, p, i) => {
        const show = page === pages[i]
        return (
        <Fragment>
          {c}
          <Element name={pages[i]}>
            {createElement(p, { show })}
          </Element>
        </Fragment>
      )}, undefined)}
      <Container fontWeight="bold" py="3em" fontSize="0.875em">
         |()
        <Text.Inline color="custom.orange"></Text.Inline>
        
        <Text.Inline color="custom.orange">Rule 144A(CDSCDX indexiTraxx Index)</Text.Inline>
        <Text.Inline color="custom.orange">N</Text.Inline>
      </Container>
      <Media lessThan="tablet">
        <StyledHeadroom disableInlineStyles>
          <FloatButton ref={ref} />
        </StyledHeadroom>
      </Media>
      <Media greaterThanOrEqual="tablet">
        <FloatButton ref={ref} />
      </Media>
    </Layout>
  )
}

export default IndexPage
